.footnote {
	color: $white;
	background: $blue;
	font-family: $dancing;
	font-size: 12px;
	line-height: 1.3;
	text-align: center;
	@include xs {
		font-size: 11px; }
	&__inner {
		padding: 10px 40px;
		margin: 0 auto;
		max-width: 700px;
		@include xs {
			padding-left: 5px;
			padding-right: 5px; }
		@include hsm {
			padding: 13px 5px; } }
	* {
		font-family: inherit; }
	span {
		font-size: 140%;
		@include xs {
			font-size: 100%; } } }
